import React from 'react'

function CardSec2() {
  return (
    <div className="cardsec2">
      <div className="card_img">
        <img src={require("../images/03.jpg")} alt="profile"/>
      </div>
      <div className="card_text">
        <h4>PROIN GRAVIDA NIBHVEL LOREM QUIS BIND</h4>
        <p>24 MARCH 2016</p>
      </div>
      <div className="d-flex">
         <div><i class="fa fa-heart"></i></div>
         <div className="travel">TRAVEL</div>
         <div><i className="fa fa-edit"></i></div>
      </div>
    </div>
  )
}

export default CardSec2
