import React from 'react'


function Section1() {
  return (
    <div className="section1">
      <div className="sec1_box">
        <div className="box1_header">
          <div>HOTEL</div>
          <div className="rental">CAR RENTAL</div>
        </div>
        <input type="date" placeholder="check in date"/>
        <input type="date"/>
        <input type="text"/>
        <input type="datetime-local"/>
        <div className="btn_wrapper">
          <button>CHECK NOW</button>
        </div>
      </div>

      <div className="sec2_box">
        <div>
          <img src={require("../images/01.jpg")} alt="profile"/>
        </div>
        <div className="state">
              <span>UAE</span>
              <span>$40:000</span>
        </div>
      </div>

      <div className="sec3_box">
        <div>
        <img src={require("../images/02.jpg")} alt="profile"/>
        </div>
        <div className="state">
              <span>TURKEY</span>
              <span>$40:000</span>
        </div>
      </div>


    </div>
  )
}

export default Section1;
